import React, { useContext, useEffect, useState } from "react";
import { GiNightSleep } from "react-icons/gi";
import { RiMoonFoggyFill } from "react-icons/ri";
import { IoBarcodeSharp } from "react-icons/io5";
import { MdKingBed } from "react-icons/md";
import { CustomContext } from "@/Context/UseCustomContext";

export default function SleepScoreTails() {
  const { sleepData } = useContext(CustomContext);
  const [sleepTailsData, setSleepTailsData] = useState([]);
  const itemWithStatus3 = sleepData?.sleep_index_common_list?.find(
    (item) => item.status === "3"
  );
  useEffect(() => {
    console.log(itemWithStatus3);

    if (sleepData) {
      setSleepTailsData([
        {
          title: "Sleep Quality",
          value: sleepData?.sleep_quality || "--",
          dataType: "percentage",
          icon: <GiNightSleep size={20} />,
        },
        {
          title: "Sleep Efficiency",
          value: sleepData?.sleep_efficiency || "--",
          dataType: "percentage",
          icon: <RiMoonFoggyFill size={20} />,
        },
        {
          title: "Avg. Sleep Flow Indicator",
          value: sleepData?.ahi || "--",
          dataType: "index",
          icon: <IoBarcodeSharp size={20} />,
        },
        {
          title: "Avg. Bed Exit Frequency",
          value:
            itemWithStatus3?.ratio >= 0 ? sleepData?.leave_bed_count : "--",
          dataType: "index",
          icon: <MdKingBed size={20} />,
        },
      ]);
    }
  }, [sleepData]);
  return (
    <div
      id="sleepTails"
      className="gap-4 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 h-fit"
    >
      {sleepTailsData?.map((item, index) => (
        <div
          key={index}
          className="flex gap-3 items-center bg-white w-full p-5 rounded-lg"
        >
          <div id="icon" className="p-3 bg-primary/10 rounded-lg">
            {item.icon}
          </div>
          <div className="flex flex-col gap-1">
            <p className="text-primary/80 text-[14px] font-semibold leading-none">
              {item.title}
            </p>
            <p className="text-primary text-[26px] font-bold leading-none">
              {item.value}
              {item.dataType === "percentage" ? "%" : ""}
            </p>
          </div>
        </div>
      ))}
    </div>
  );
}
